import React, { Component } from 'react';
import "react-responsive-carousel/lib/styles/carousel.css";
import { Carousel } from 'react-responsive-carousel';
import "../style/Demo.css" ;

class DemoCarousel extends Component {
    render() {
      
        return (
            <div className="wrapper">
            <Carousel  autoplay showThumbs={false} showStatus={false}>
                <div>
                    <img src="https://cdn.pixabay.com/photo/2019/04/29/20/41/amsterdam-4167026__340.png" style={{"-WebkitTransform":"translateZ(0)","transform":"translateZ(0)"}}/>
                    <p className="legend">RWTH</p>
                </div>
                <div>
                    <img src="PIC02.png" />
                    <p className="legend">RWTH</p>
                </div>
                <div>
                    <img src="PIC03.png" />
                    <p className="legend">Lecture</p>
                </div>
                <div>
                    <img src="../../PIC04.png" />
                    <p className="legend">Lecture</p>
                </div>
                <div>
                    <img src="../../PIC05.png" />
                    <p className="legend">Lecture</p>
                </div>
            </Carousel>
            </div>
        );
    }
};

export default DemoCarousel;